@import './Montserrat.css';


/* start global styles + overrides*/
  *{
    padding: 0;
    margin:0;
  }
  /* global colors */
  :root{
    --grey-url : #909090;
    --grey-blue : #D0D5DE;   
    --black-open : #1d1d1df2;
    --prim-blue : #4E69A3;
    --sec-blue : #5085E8;
    --dark-gry : #454545;
    --side-grey: #F2F2F5;
  }

  /* Override a links style*/
  a {
    text-decoration: none;
    color: inherit;
  }

/* end global styles + overrides*/

/* start project style*/
body {
  font-family: "Montserrat";
  background: #F6F6F9;
}
#root {
  display: flex;
  justify-content: space-between;
}

/* main cont */

.main-cont {
  display: flex;
  justify-content: center;
  flex-grow: 1;
  box-shadow: 4px 0 14px 0px grey;
  z-index: 2;
}

/* News Feed Page Styles */

.posts-holder {
  max-width: 550px;
}
@media screen and (max-width:600px){
  .posts-holder {
    width: 95%;
    max-width: 99%;
  }
}



/* Sider Styles */

.sider-holder {

  min-width: 220px;
  background: var(--side-grey);
  padding-top:20px ;

  padding-left: 30px;
  position: relative;
}


.sider_inner {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  position: fixed;
  min-width: 200px;
}
.sider_inner .sider_logo  {
  width: min-content;
  color: var(--sec-blue);
  text-transform: capitalize;
  margin-bottom: 30px;
}


.sider_nav-holder .slider_nav-holder_title{
  color: var(--dark-gry);
  font-weight: 500;
  width: min-content;
  margin-bottom: 10px;
}

.sider_nav-holder .nav_nodes{
  display: flex;
  flex-direction: column;
  justify-content: flex-start;

}
.sider_nav-holder .nav_nodes ul{
  list-style-type: none;
}
.sider_nav-holder .nav_nodes ul li{
  padding: 5px 0;
  color: var(--dark-gry);
  font-weight: 300;
  position: relative;
  margin: 4px 0;
  display: flex;
}
.sider_nav-holder .nav_nodes ul li a{
  flex-grow:1;
}
.sider_nav-holder .nav_nodes ul li.active{
  font-weight: 500;
}
.sider_nav-holder .nav_nodes ul li i{
  margin-right : 15px;
}

.sider_nav-holder .nav_nodes ul li::after {
  position: absolute;
  top: 0;
  left: -5px;
  width: 100%;
  height: 100%;
  z-index: -1;
  content: '';
  transition: .2s all ease-in-out;
}

.sider_nav-holder .nav_nodes ul li:hover::after {
  background: var(--grey-blue);
}