/* start global styles + overrides*/
  /* global colors */
  :root{
    --grey-url : #909090;
    --grey-blue : #D0D5DE;   
    --black-open : #1d1d1df2;
    --prim-blue : #4E69A3;
    --sec-blue : #5085E8;
    --dark-gry : #454545;
  }


/* end global styles + overrides*/





.post-container {
  background: #FFFFFF;
  padding: 20px;
  border: 1px solid #F1F1F2;
  border-radius: 5px;
  box-shadow: 0px 0px 2px 0px #62626236 ;
  box-sizing: border-box;
  width: 100%;
  margin: 15px 0;
}


/* start  post header style */

.post-header .post-header-inner {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 15px;
}


  /* post header info */
    .post-header .post-header-info {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }

    /* user avatar */
    .post-header .post-header-info .user-avatar{
      height: 50px;
      width: 50px;
      border:1px solid rgba(242, 241, 242, 0.432);
      border-radius: 50%;
      margin-right: 15px;
      overflow: hidden;
    } 

    .post-header .post-header-info .user-avatar img{
      max-width: 100%;
      transform: scale(1.2);
    } 

    /* post info */

    .post-header .post-header-info .post-info  a{
      line-height: 20px;
    } 
    .post-header .post-header-info .post-info .post-user-name a{
      font-size: 16px;
      color: var(--dark-gry);
      font-weight: 500;
      transition: .2s filter ease-in-out;
    } 
    .post-header .post-header-info .post-info .post-user-name:hover a{
      filter: brightness(80%);
    }   
    .post-header .post-header-info .post-info .post-info-date a{
      font-size: 13px;
      color: var(--dark-gry);
      font-weight: 300;
      border-bottom: 1px solid transparent;
      transition: .1s border ease-in-out;
    } 
    .post-header .post-header-info .post-info .post-info-date:hover a{
      border-bottom: 1px solid #45454579;
    } 

    /* post header options */ /* todo replace this cont with svg ++ styles */
    .post-header .post-header-options a{
      font-size: 25px;
      
      line-height: 5px;
      color: var(--grey-url);
    }

  /* end  post header style */

  /* start post Content style*/
    .post-content {
      margin-bottom: 10px;
      overflow: hidden;
    } 
    .post-content .post-text {
      font-size: 15px;
      color: var(--dark-gry);    
      display: block;
      margin-bottom: 10px;  
    }

    .post-content .post-pict img {
      width: 100%;
      border-radius: 3px;
      max-height: 340px;
      user-select: none;
      -webkit-user-drag: none;
      

    }
    

/* end post Content style*/

/* start post footer style*/

  .post-footer .post-footer-inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .post-footer .post-footer-stat  {
    display: flex;
    align-items: center;
    justify-content: space-between; 
  }

  .post-footer .post-footer-stat .stat-holder {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-right: 16px;
  }



  .post-footer .post-footer-stat .stat-holder svg {
    color: rgb(156, 156, 156);
    height: 25px;
    width: 25px;    
    transition: .3s color ease-in-out;
  }
  .post-footer .post-footer-stat .stat-holder .img{
    height: 25px;
  }
  .post-footer .post-footer-stat .stat-holder .count {
    margin-left: 5px;
    color: var(--dark-gry);
    font-weight: 300;
    user-select: none;
    transition: .3s color ease-in-out;
  }  

  .post-footer .post-footer-stat .stat-holder:hover .count,
  .post-footer .post-footer-stat .stat-holder:hover svg {    
    color: var(--sec-blue);    
  }  

  .post-footer .post-footer-fav img {
    height: 25px;
  }

  .post-footer .post-footer-fav svg {
    color: rgb(156, 156, 156);
    fill: transparent;
    height: 25px;
    width: 25px;    
    transition: .3s fill ease-in-out;
  }
  .post-footer .post-footer-fav .fav:hover svg {
    fill: rgb(243, 217, 73);
  }
  .post-footer .post-footer-fav .fav.active svg{
    fill: rgb(255, 217, 8);
    color: transparent;
  }

/* end post footer style*/



  
